div.products-landing {
    padding: 75px 0;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    gap: 50px;
    background-color: var(--faded-blue);
}

div.products-landing > div {
    position: relative;
    z-index: 1;
}

div.products-landing div.nav-button-container {
    justify-content: space-between;
}

div.products-landing img.landing-image {
    object-position: center 38%;
    opacity: 0.65;
}

@media screen and (max-width: 615px) {
    div.products-landing div.nav-button-container {
        flex-direction: column;
        align-items: center;
        gap: 2rem;
    }
}
